<template>
  <v-dialog v-model="show" persistent width="640">
    <v-card v-if="type === 0">
      <v-layout align-center justify-center fill-height column>
        <v-card-title>
          <h2>실패</h2>
        </v-card-title>
        <v-card-text class="text-xs-center">
          <h3>
            도전에 실패하셨습니다.
            <br>다시 도전하시겠습니까?
          </h3>
        </v-card-text>
        <v-spacer></v-spacer>
        <v-card-actions>
          <v-btn color="primary" @click="tryagain">예</v-btn>
          <v-btn color="secondary" @click="quit">아니오</v-btn>
        </v-card-actions>
      </v-layout>
    </v-card>
    <v-card v-else>
      <v-layout align-center justify-center fill-height column>
        <v-card-title>
          <h2>성공</h2>
        </v-card-title>
        <v-card-text class="text-xs-center">
          <h3>
            보물을 성공적으로 보관했습니다.
            <br>다시 한번 도전하시겠습니까?
          </h3>
        </v-card-text>
        <v-spacer></v-spacer>
        <v-card-actions>
          <v-btn color="primary" @click="tryagain">예</v-btn>
          <v-btn color="secondary" @click="quit">아니오</v-btn>
        </v-card-actions>
      </v-layout>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  data() {
    return {
      localShow: false
    };
  },
  props: ["show", "type"],
  methods: {
    tryagain: function() {
      this.setStates(true);
      this.$emit("update:show", false);
      this.$root.$emit("tryagain");
    },
    quit: function() {
      this.setStates(false);
      this.$emit("update:show", false);
      this.$root.$emit("quit");
    }
  }
};
</script>
